body{
    display: flex;
    align-items: center;
    min-height: 100vh;
}
body #root{
    width: 100%;
}
.title{
    text-decoration: underline;
    margin: 10px 0 20px;
}
.redux-form{
    padding: 20px;
    background-color: #333;
    border-radius: 4px;
    color: #fff;
}
.routing-list{
    display: flex;
    list-style-type: none;
    justify-content: center;
    margin-bottom: 30px;
    padding: 0;
}
.routing-list li{
    margin: 5px;
}
.routing-list li a{
    background-color: #333;
    padding: 5px 10px;
    border-radius: 4px;
    color: #fff;
    display: block;
    text-decoration: none;
}
.routing-list li a.active{
    background-color: #f03434;
    color: #fff;
}
.counter-container{
    text-align: center;
}
.counter-value-container{
    display: block;
}
.counter-value{
    font-size: 120px;
    background-color: #333;
    color: #f03434;
    padding: 50px;
    display: inline-block;
    height: 200px;
    width: 200px;
    line-height: 85px;
}
.prop-data-container{
    background-color: #333;
    padding: 20px;
    color: #fff;
    margin: 10px 0;
    border-left: 5px solid #f03434;
}
.ref-container{
    background-color: #333;
    color: #fff;
    padding: 30px;
    width: 500px;
    margin: 20px auto;
    border-radius: 5px;
}